// src/components/API/API.js

import axios from "axios";

// Define the base URL for your Express backend
export const API_URL = "http://localhost:8085"; // Use a named export for flexibility

// Create an Axios instance for API requests
const api = axios.create({
  baseURL: API_URL, // Set the base URL for all requests
  headers: {
    "Content-Type": "application/json", // Ensure JSON is used for communication
  },
  timeout: 5000, // Optional: Set a 5-second timeout for requests
});

// Export the Axios instance as default and API_URL as a named export
export default api;